.experience-grid {
  display: flex;
  align-items: flex-start; /* Align items at the top */
}

.col-left {
  flex: 1;
}

.line {
  border-left: 2px solid #ccc;
  height: 100%; /* Vertical line spanning the full height */
  margin: 0 16px; /* Adjust as needed */
}

.col-right {
  flex: 1;
}

.p {
  word-spacing: 0;
  padding: 0;
}